.about {
  max-width: 1364px;
  margin: 0 auto 12px auto;
  padding: 50px 0;
  border-radius: 20px;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: linear-gradient(
      360deg,
      #000000 0%,
      rgba(12, 14, 19, 0.56) 61.67%
    ),
    url(../../assets/images/about/aboutBg@1x.jpg);

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: linear-gradient(
        360deg,
        #000000 0%,
        rgba(12, 14, 19, 0.56) 61.67%
      ),
      url(../../assets/images/about/aboutBg@2x.webp),
      url(../../assets/images/about/aboutBg@2x.jpg);
  }

  @media (min-device-pixel-ratio: 3),
    (min-resolution: 288dpi),
    (min-resolution: 3dppx) {
    background-image: linear-gradient(
        360deg,
        #000000 0%,
        rgba(12, 14, 19, 0.56) 61.67%
      ),
      url(../../assets/images/about/aboutBg@3x.webp),
      url(../../assets/images/about/aboutBg@3x.jpg);
  }

  &Content {
    text-align: center;
    font-size: 30px;
    line-height: 1.19;
    letter-spacing: 0.02em;

    @media (min-width: 768px) {
      font-size: 60px;
    }
  }

  &Title {
    font-family: SFproMedium;
  }

  &Years {
    font-family: SFproSemiBold;
  }
}

@supports (
  background-image:
    linear-gradient(360deg, #000000 0%, rgba(12, 14, 19, 0.56) 61.67%),
  url('../../assets/images/about/aboutBg@1x.webp')
) {
  .about {
    background-image: linear-gradient(
        360deg,
        #000000 0%,
        rgba(12, 14, 19, 0.56) 61.67%
      ),
      url('../../assets/images/about/aboutBg@1x.webp');

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: linear-gradient(
          360deg,
          #000000 0%,
          rgba(12, 14, 19, 0.56) 61.67%
        ),
        url(../../assets/images/about/aboutBg@2x.webp);
    }

    @media (min-device-pixel-ratio: 3),
      (min-resolution: 288dpi),
      (min-resolution: 3dppx) {
      background-image: linear-gradient(
          360deg,
          #000000 0%,
          rgba(12, 14, 19, 0.56) 61.67%
        ),
        url(../../assets/images/about/aboutBg@3x.webp);
    }
  }
}
